function ScheduleGrid({ sessions, weekDates, dayFilter }: { sessions: ProcessedSession[], weekDates: Date[], dayFilter: string }) {
    const daysOfWeek = ['Saturday', 'Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday'];
    const timeSlots = Array.from({ length: 14 }, (_, i) => 10 + i); // 10 AM to 11 PM

    const filteredDays = dayFilter === 'All' ? daysOfWeek : [dayFilter];

    return (
        <Card className="overflow-hidden">
            <ScrollArea className="h-[70vh] w-full">
                <div className="relative grid grid-cols-1 md:grid-cols-7" style={{ gridTemplateRows: 'auto repeat(28, 30px)' }}>
                    {/* Headers */}
                    <div className="sticky top-0 z-10 hidden md:grid md:grid-cols-7 col-span-full bg-card border-b">
                        {daysOfWeek.map((day, index) => (
                           <div key={day} className={`p-2 text-center font-semibold ${index > 0 ? 'border-l' : ''}`}>{day}</div>
                        ))}
                    </div>

                    {/* Time Gutter */}
                    <div className="row-span-full hidden md:block">
                        {timeSlots.map(hour => (
                            <div key={hour} className="relative h-[60px] flex items-start justify-end pr-2" style={{ gridRowStart: (hour - 10) * 2 + 2 }}>
                               <span className="text-xs text-muted-foreground -translate-y-1/2">
                                {hour % 12 === 0 ? 12 : hour % 12} {hour < 12 || hour === 24 ? 'AM' : 'PM'}
                               </span>
                            </div>
                        ))}
                    </div>
                    
                    {/* Sessions */}
                    <div className="md:col-start-1 md:col-span-7 grid grid-cols-1 md:grid-cols-7 h-full">
                        {daysOfWeek.map((day, dayIndex) => (
                            <div key={day} className={`relative ${dayIndex > 0 ? 'border-l' : ''} col-span-1`} style={{ gridTemplateRows: 'repeat(28, 30px)' }}>
                                {/* Hour lines */}
                                {timeSlots.map((_, i) => <div key={i} className="h-[60px] border-b" style={{ gridRowStart: i * 2 + 1 }}></div>)}
                                
                                {/* Session cards */}
                                {sessions.filter(s => s.day === day).map(session => (
                                    <div 
                                        key={session.id} 
                                        className="absolute w-full p-1"
                                        style={{ top: `${(session.startRow - 10) * 60}px`, height: `${session.duration * 60}px`}}
                                    >
                                        <Card className="h-full bg-secondary/50 flex flex-col overflow-hidden">
                                           <CardHeader className="p-2">
                                                <p className="font-semibold text-xs leading-tight">{session.specialization}</p>
                                                <p className="text-xs text-muted-foreground">{session.time} - {session.endTime}</p>
                                           </CardHeader>
                                           <CardContent className="p-2 flex-grow overflow-y-auto">
                                                <div className="space-y-1">
                                                    {session.students.map(student => (
                                                        <div key={student.id} className="text-xs bg-background p-1 rounded-sm">{student.name}</div>
                                                    ))}
                                                </div>
                                           </CardContent>
                                        </Card>
                                    </div>
                                ))}
                            </div>
                        ))}
                    </div>

                </div>
            </ScrollArea>
        </Card>
    );
}

