

"use client";

import { useForm, Controller } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import { z } from "zod";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog";
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Checkbox } from "@/components/ui/checkbox";
import { useToast } from "@/hooks/use-toast";
import { useState, useMemo } from "react";
import { Loader2 } from "lucide-react";
import { StudentProfile, Semester, Session, SessionStudent } from "@/lib/types";
import { ScrollArea } from "./ui/scroll-area";
import { useDatabase } from "@/context/database-context";

const enrollStudentSchema = z.object({
  studentId: z.string().min(1, "Please select a student."),
  sessionIds: z.array(z.string()).min(1, "Please select at least one session."),
});

type EnrollStudentFormValues = z.infer<typeof enrollStudentSchema>;

interface EnrollStudentDialogProps {
  isOpen: boolean;
  onOpenChange: (open: boolean) => void;
  students: StudentProfile[];
  semester: Semester;
  onEnrollmentSuccess: () => void;
}

export function EnrollStudentDialog({ isOpen, onOpenChange, students, semester, onEnrollmentSuccess }: EnrollStudentDialogProps) {
  const { toast } = useToast();
  const { updateStudent, updateSemester } = useDatabase();
  const [isLoading, setIsLoading] = useState(false);
  const [selectedTeacher, setSelectedTeacher] = useState<string>('');


  const form = useForm<EnrollStudentFormValues>({
    resolver: zodResolver(enrollStudentSchema),
    defaultValues: {
      studentId: "",
      sessionIds: [],
    },
  });
  
  const studentId = form.watch('studentId');
  
  const allSessionsByTeacher = useMemo(() => {
    if (!semester || !semester.masterSchedule) return {};
    return Object.entries(semester.masterSchedule).reduce((acc, [teacher, schedule]) => {
        acc[teacher] = Object.entries(schedule).flatMap(([day, sessions]) =>
            sessions.map(session => ({
                ...session,
                teacher,
                day
            }))
        );
        return acc;
    }, {} as Record<string, (Session & { day: string, teacher: string })[]>);
  }, [semester]);

  const availableTeachers = useMemo(() => Object.keys(allSessionsByTeacher), [allSessionsByTeacher]);


  async function onSubmit(data: EnrollStudentFormValues) {
    setIsLoading(true);
    const studentToEnroll = students.find(s => s.id === data.studentId);

    if (!studentToEnroll) {
      toast({ title: "Error", description: "Student not found.", variant: 'destructive' });
      setIsLoading(false);
      return;
    }

    try {
        const updatedMasterSchedule = JSON.parse(JSON.stringify(semester.masterSchedule)); // Deep copy
        const newEnrollments = [];

        for (const sessionIdWithTeacher of data.sessionIds) {
            const [sessionId, teacher, day] = sessionIdWithTeacher.split('|');

            newEnrollments.push({ semesterId: semester.id, teacher, sessionId });

            const daySessions = updatedMasterSchedule[teacher]?.[day];
            if(daySessions) {
                const sessionIndex = daySessions.findIndex((s: Session) => s.id === sessionId);
                if (sessionIndex !== -1) {
                    const studentInSession = daySessions[sessionIndex].students.some((s: any) => s.id === data.studentId);
                    if (!studentInSession) {
                        const studentData: SessionStudent = { id: studentToEnroll.id, name: studentToEnroll.name, attendance: null, pendingRemoval: false };
                        updatedMasterSchedule[teacher][day][sessionIndex].students.push(studentData);
                    }
                }
            }
        }
        
        const updatedEnrolledIn = [...studentToEnroll.enrolledIn, ...newEnrollments];
        
        const semesterSuccess = await updateSemester(semester.id, { masterSchedule: updatedMasterSchedule });
        const studentSuccess = await updateStudent(studentToEnroll.id, { enrolledIn: updatedEnrolledIn });

        setIsLoading(false);

        if (semesterSuccess && studentSuccess) {
            toast({ title: "Enrollment Successful", description: `${studentToEnroll.name} has been enrolled.` });
            onEnrollmentSuccess();
            onOpenChange(false);
            form.reset();
        }
    } catch (error: any) {
        setIsLoading(false);
        console.error("Enrollment failed:", error);
        toast({ title: "Enrollment Failed", description: error.message, variant: 'destructive'});
    }
  }

  return (
    <Dialog open={isOpen} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>Enroll Student in Semester</DialogTitle>
          <DialogDescription>
            Select a student and the classes they will attend for the "{semester.name}" semester.
          </DialogDescription>
        </DialogHeader>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="studentId"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Student</FormLabel>
                  <Select onValueChange={(value) => { field.onChange(value); setSelectedTeacher(''); form.setValue('sessionIds', []); }} defaultValue={field.value}>
                    <FormControl>
                      <SelectTrigger>
                        <SelectValue placeholder="Select a student to enroll" />
                      </SelectTrigger>
                    </FormControl>
                    <SelectContent>
                      {students.map(student => (
                        <SelectItem key={student.id} value={student.id}>{student.name} ({student.level})</SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  <FormMessage />
                </FormItem>
              )}
            />
            
            <FormItem>
                <FormLabel>Teacher</FormLabel>
                <Select value={selectedTeacher} onValueChange={setSelectedTeacher} disabled={!studentId}>
                    <FormControl>
                        <SelectTrigger>
                            <SelectValue placeholder="Select a teacher" />
                        </SelectTrigger>
                    </FormControl>
                    <SelectContent>
                        {availableTeachers.map(teacher => (
                            <SelectItem key={teacher} value={teacher}>{teacher}</SelectItem>
                        ))}
                    </SelectContent>
                </Select>
            </FormItem>
            
            {selectedTeacher && (
                 <FormField
                control={form.control}
                name="sessionIds"
                render={() => (
                    <FormItem>
                        <FormLabel>Classes</FormLabel>
                        <ScrollArea className="h-56 w-full rounded-md border p-4">
                            <div className="space-y-4">
                                {allSessionsByTeacher[selectedTeacher] ? (
                                    allSessionsByTeacher[selectedTeacher].map(session => (
                                    <FormField
                                        key={`${session.id}|${session.teacher}|${session.day}`}
                                        control={form.control}
                                        name="sessionIds"
                                        render={({ field }) => (
                                        <FormItem className="flex flex-row items-start space-x-3 space-y-0">
                                            <FormControl>
                                            <Checkbox
                                                checked={field.value?.includes(`${session.id}|${session.teacher}|${session.day}`)}
                                                onCheckedChange={(checked) => {
                                                return checked
                                                    ? field.onChange([...(field.value || []), `${session.id}|${session.teacher}|${session.day}`])
                                                    : field.onChange(
                                                        field.value?.filter(
                                                        (value) => value !== `${session.id}|${session.teacher}|${session.day}`
                                                        )
                                                    );
                                                }}
                                            />
                                            </FormControl>
                                            <FormLabel className="font-normal">
                                                {session.day} at {session.time}{session.endTime ? ` - ${session.endTime}` : ''} ({session.specialization})
                                            </FormLabel>
                                        </FormItem>
                                        )}
                                    />
                                    ))
                                ) : <p className="text-sm text-muted-foreground text-center">No sessions for this teacher.</p>}
                            </div>
                        </ScrollArea>
                        <FormMessage />
                    </FormItem>
                )}
                />
            )}
           
            <DialogFooter>
                <Button type="submit" className="w-full" disabled={isLoading}>
                    {isLoading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                    Enroll Student
                </Button>
            </DialogFooter>
          </form>
        </Form>
      </DialogContent>
    </Dialog>
  );
}
